import { Overlay } from "ol";
import { getLength, getArea } from "ol/sphere";
import { Style, Fill, Stroke, Circle } from "ol/style";
import { Draw } from "ol/interaction";
import { Vector } from "ol/layer"
/**
 * tooltip
 * @param {Element} helpTooltipElement  
 * @param {ol.Map} map  
 */
export function createHelpTooltip({ helpTooltipElement, map }) {
  if (helpTooltipElement) {
    helpTooltipElement.parentNode.removeChild(helpTooltipElement);
  }
  const newHelpTooltipElement = document.createElement("div");
  newHelpTooltipElement.className = "tooltip hidden";
  const helpTooltip = new Overlay({
    element: newHelpTooltipElement,
    offset: [15, 0],
    positioning: "center-left",
  });
  map.addOverlay(helpTooltip);
  return [newHelpTooltipElement, helpTooltip]
}


/**
* tooltip
* @param {Element} measureTooltipElement  
* @param {ol.Map} map  
*/
export function createMeasureTooltip({ measureTooltipElement, map }) {
  if (measureTooltipElement) {
    measureTooltipElement.parentNode.removeChild(measureTooltipElement);
  }
  const newMeasureTooltipElement = document.createElement("div");
  newMeasureTooltipElement.className = "tooltip tooltip-measure";
  const measureTooltip = new Overlay({
    element: newMeasureTooltipElement,
    offset: [0, -15],
    positioning: "bottom-center",
  });
  map.addOverlay(measureTooltip);
  return [newMeasureTooltipElement, measureTooltip]
}

/**
 * 
 * @param {boolean} checked 
 * @param {ol.geom.LineString} line
 * @param {ol.Map} map  
 * @return {string}
 */
export function formatLength({ checked, line, map }) {
  let length;
  if (checked) {
    //
    const sourceProj = map.getView().getProjection(); //
    length = getLength(line, {
      projection: sourceProj,
      radius: 6378137,
    });
  } else {
    length = Math.round(line.getLength() * 100) / 100; //
  }
  let output;
  if (length > 100) {
    output = Math.round((length / 1000) * 100) / 100 + " " + "km"; //KM
  } else {
    output = Math.round(length * 100) / 100 + " " + "m"; //m
  }
  return output; //
}

/**
 * 
 * @param {boolean} checked 
 * @param {ol.geom.Polygon} polygon
 * @param {ol.Map} map  
 * @return {string}
 */
export function formatArea({ checked, polygon, map }) {
  let area;
  if (checked) {
    //
    const sourceProj = map.getView().getProjection(); //
    const geom = /** @type {ol.geom.Polygon} */ (
      polygon.clone().transform(sourceProj, "EPSG:4326")
    ); //EPSG:4326
    area = Math.abs(
      getArea(geom, { projection: sourceProj, radius: 6378137 })
    ); //
  } else {
    area = polygon.getArea(); //
  }
  let output;
  if (area > 10000) {
    output =
      Math.round((area / 1000000) * 100) / 100 + " " + "km<sup>2</sup>"; //KM
  } else {
    output = Math.round(area * 100) / 100 + " " + "m<sup>2</sup>"; //m
  }
  return output; //
}
/**
 * 
 * @param {ol.source.Vector} source 
 * @param {ol.geom.GeometryType} type 
 * @return 
 */
export function drawGeometricFigure({ source, type }) {
  return new Draw({
    source, //
    type, /** @type {ol.geom.GeometryType}  */
    style: new Style({
      //
      fill: new Fill({
        color: "rgba(255, 255, 255, 0.2)",
      }),
      stroke: new Stroke({
        color: "rgba(0, 0, 0, 0.5)",
        lineDash: [10, 10],
        width: 2,
      }),
      image: new Circle({
        radius: 5,
        stroke: new Stroke({
          color: "rgba(0, 0, 0, 0.7)",
        }),
        fill: new Fill({
          color: "rgba(255, 255, 255, 0.2)",
        }),
      }),
    }),
  });
}

// 
export function createVector(source) {
  return new Vector({
    source,
    style: new Style({
      //
      fill: new Fill({
        color: "rgba(255, 255, 255, 0.2)", //
      }),
      stroke: new Stroke({
        color: "#ffcc33", //
        width: 2, // 
      }),
      image: new Circle({
        radius: 7,
        fill: new Fill({
          color: "#ffcc33",
        }),
      }),
    }),
  })
}