<template>
  <div id="map"></div>
  <div id="menu">
    
    <el-select
      v-model="state.typeSelect"
      placeholder="Select"
      size="small"
      @change="handleChange"
      class="select"
    >
      <el-option
        v-for="item in state.options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      />
    </el-select>
    <el-checkbox
      v-model="state.checked"
      label=""
      size="large"
    />
  </div>
</template>

<script setup>
import 'ol/ol.css'
import { onMounted, reactive } from 'vue'
import { Map, View } from 'ol'
import { Tile as TileLayer } from 'ol/layer'
import { Vector } from 'ol/source'
import { Polygon, LineString } from 'ol/geom'
import { unByKey } from 'ol/Observable'
import { MAPURL, ATTRIBUTIONS } from '@/constants'
import updateMapSize from '@/hooks/updateMapSize'
import XYZ from 'ol/source/XYZ'
import {
  createHelpTooltip,
  createMeasureTooltip,
  formatLength,
  formatArea,
  drawGeometricFigure,
  createVector,
} from './index'

const raster = new TileLayer({
  source: new XYZ({
    attributions: ATTRIBUTIONS,
    url: MAPURL,
    maxZoom: 20,
  }),
})

/**
 *  
 * @type {string}
 */
const continuePolygonMsg = ''
/**
 * 
 * @type {string}
 */
const source = new Vector()
const continueLineMsg = ''
const state = reactive({
  checked: false,
  typeSelect: 'length',
  options: [
    {
      label: '',
      value: 'length',
    },
    {
      label: '',
      value: 'area',
    },
  ],
  map: null,
  sketch: null, // 
  helpTooltipElement: null, // 
  helpTooltip: null, // 
  measureTooltipElement: null, // 
  measureTooltip: null, // 
  draw: null,
})
//map
const initMap = () => {
  state.map = new Map({
    target: 'map',
    //
    layers: [
      //
      raster,
    ],
    view: new View({
      projection: 'EPSG:4326', // EPSG:4326EPSG:3 857
      center: [0, 0], // 
      //
      zoom: 5,
    }),
  })
  // 
  updateMapSize(state.map)
}

// 
const loadVector = () => {
  // 
  const vector = createVector(source)
  state.map.addLayer(vector)
}
/**
 * 
 * @param {ol.MapBrowserEvent} evt
 */
const pointerMoveHandler = (evt) => {
  if (evt.dragging) {
    return
  }
  /** @type {string} */
  let helpMsg = '' //
  //
  if (state.sketch) {
    const getGeometry = state.sketch.getGeometry()
    if (getGeometry instanceof Polygon) {
      helpMsg = continuePolygonMsg //
    } else if (getGeometry instanceof LineString) {
      helpMsg = continueLineMsg //
    }
  }
  state.helpTooltipElement.innerHTML = helpMsg //
  state.helpTooltip.setPosition(evt.coordinate) //
  state.helpTooltipElement.classList.remove('hidden')
}

/**
 * 
 */
const addInteraction = () => {
  const type = state.typeSelect === 'area' ? 'Polygon' : 'LineString'
  state.draw = drawGeometricFigure({ source, type })
  state.map.addInteraction(state.draw)
  //
  ;[state.measureTooltipElement, state.measureTooltip] = createMeasureTooltip({
    measureTooltipElement: state.measureTooltipElement,
    map: state.map,
  })
  console.log(state.measureTooltipElement)
  //
  ;[state.helpTooltipElement, state.helpTooltip] = createHelpTooltip({
    helpTooltipElement: state.helpTooltipElement,
    map: state.map,
  })
  let listener
  //
  state.draw.on(
    'drawstart',
    function (evt) {
      // set sketch
      state.sketch = evt.feature //

      /** @type {ol.Coordinate|undefined} */
      let tooltipCoord = evt.coordinate // 
      //change
      listener = state.sketch.getGeometry().on('change', function (evt) {
        const geom = evt.target //
        let output
        if (geom instanceof Polygon) {
          output = formatArea({
            checked: state.checked,
            polygon: geom,
            map: state.map,
          }) //
          tooltipCoord = geom.getInteriorPoint().getCoordinates() //
        } else if (geom instanceof LineString) {
          output = formatLength({
            checked: state.checked,
            line: geom,
            map: state.map,
          }) //
          tooltipCoord = geom.getLastCoordinate() //
        }
        state.measureTooltipElement.innerHTML = output //
        state.measureTooltip.setPosition(tooltipCoord) //
      })
    },
    this
  )
  //
  state.draw.on(
    'drawend',
    function () {
      state.measureTooltipElement.className = 'tooltip tooltip-static' //
      state.measureTooltip.setOffset([0, -7])
      // 
      state.sketch = null //
      // 
      state.measureTooltipElement = null //
      //
      ;[state.measureTooltipElement, state.measureTooltip] =
        createMeasureTooltip({
          measureTooltipElement: state.measureTooltipElement,
          map: state.map,
        })
      console.log(state.measureTooltipElement)
      unByKey(listener)
    },
    this
  )
}

// /
const handleChange = () => {
  state.map.removeInteraction(state.draw) //
  addInteraction() //
}
onMounted(() => {
  initMap()
  loadVector()

  state.map.on('pointermove', pointerMoveHandler) //
  //
  state.map.getViewport().addEventListener('mouseout', () => {
    state.helpTooltipElement.classList.add('hidden')
  })
  addInteraction() //
})
</script>

