<style scoped>
#map {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#menu {
  width: 450px;
  position: absolute;
  left: 20px;
  bottom: 20px;
  color: #fff;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/**
    * 
    */
:deep(.tooltip) {
  position: relative;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  color: white;
  padding: 4px 8px;
  opacity: 0.7;
  white-space: nowrap;
}

:deep(.tooltip-measure) {
  opacity: 1;
  font-weight: bold;
}

:deep(.tooltip-static) {
  background-color: #ffcc33;
  color: black;
  border: 1px solid white;
}
:deep(.tooltip-measure:before),
:deep(.tooltip-static:before) {
  border-top: 6px solid rgba(0, 0, 0, 0.5);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  content: '';
  position: absolute;
  bottom: -6px;
  margin-left: -7px;
  left: 50%;
}

:deep(.tooltip-static:before) {
  border-top-color: #ffcc33;
}
.mobile #menu {
  width: 310px;
  font-size: 14px;
  bottom: 40px;
}
.mobile .select {
  width: 80px;
}
</style>
